import React from "react"

export default class Faq extends React.Component {
    render() {
        return (
            <div className='std-page center-page-x center' >
                <h1>FAQ</h1>
                <div style={{ maxWidth: '800px' }}>
                    <div >
                        <h4>I clicked on the View in AR button in the viewer but cannot see the model in augmented reality.</h4>
                        <p>
                            Unfortunately not all mobiles support AR. Please click <a href='https://developers.google.com/ar/devices'>here</a> for a list of supported devices.
                        </p>
                        <h4>Which 3d model file formats can be uploaded?</h4>
                        <p>
                            Currently glTF, obj and fbx files can be uploaded. More 3d model file formats will be supported soon.<br />
                            You can convert another format to glTF using an online converter such AnyConv.com.<br />
                            Some 3d modelling applications such as Blender can export glTF files.
                        </p>
                        <h4>What is a glTF file?</h4>
                        <p>
                            glTFâ„¢ (GL Transmission Format) is a royalty-free specification for the efficient transmission and loading of 3d scenes and models by applications.
                            It is one of the most common file formats for 3d web applications. <br />
                        </p>
                        <h4>How can I get a 3d model of my product?</h4>
                        <p>
                            Ezyvue has partnered with a number of companies that specialize in building 3d models for the web and AR.<br />
                            Please contact us and we will be able to walk you through the process.
                        </p>
                    </div>
                </div>
            </div>
        );
    }
}
